.list{display:-webkit-box;display:-moz-flex;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap}.product-item{background:#fff}.product-item .title{font-size:14px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;margin-top:10px}.product-item .thumbnail{width:100%;vertical-align:middle}.product-item.product-item-def{width:50%;padding:10px;border-bottom:4px solid #f3f3f3}.product-item.product-item-def:nth-child(2n-1){border-right:2px solid #f3f3f3}.product-item.product-item-def:nth-child(2n){border-left:2px solid #f3f3f3}.product-item.product-item-def .content{position:relative;padding-right:30px}.product-item.product-item-def .content .icon{position:absolute;font-size:24px;right:0;bottom:0}.product-item.product-item-def .content .icon:after{content:' ';position:absolute;top:-10px;left:-10px;right:-10px;bottom:-10px}.product-item.product-item-rec{width:100%}.product-item.product-item-rec .title{padding:0 10px;text-align:center}.item.product-item-cart{width:100%;border-right:none;padding-left:148px;padding-bottom:8px}.item.product-item-cart .title{margin-top:5px}.item.product-item-cart .thumbnail{left:58px}.item.product-item-cart .checkbox{right:auto}.item.product-item-cart .weui_cell{margin-top:10px;padding:0}.item.product-item-cart .weui_cell:before{display:none}.item.product-item-cart .weui_cell_primary{display:none}.item.product-item-cart .vux-number-input,.item.product-item-cart .vux-number-selector{height:28px}.item.product-item-cart .icon{font-size:24px;color:#999}



