.cate-item{width:50%;height:150px;padding:10px 10px 0}.cate-item:nth-child(2n-1){padding-right:5px}.cate-item:nth-child(2n){padding-left:5px}.cate-item .title{font-size:16px}.cate-item .sub-title{font-size:11px;color:#999}.cate-item .title,.cate-item .sub-title{margin-top:0;margin-bottom:5px}.cate-item>.cate-item-border{position:relative;height:100%;padding:10px;border:1px solid #f3f3f3}.cate-item>.cate-item-border>img{width:60%;position:absolute;right:0;bottom:0}



